nav {
    position: absolute;
    top: 101px;
    left: -602px;
    z-index: 100;

    width: 602px;
    height: calc(100vh - 101px);

    background: #ffffff;

    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    
    transition: 0.5s;
}

nav.mobile {
    height: 100vh;
    width: 100vw;
    top: 0;
    left: -100vw;
}

nav.menu-open, nav.mobile.menu-open {
    left: 0; 
}

.wrapper {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
}

.wrapper.mobile {
    position: fixed;
}

.wrapper.wrapper-show {
