}

.news-card.simple .news-card-text p {
    font-style: normal;
    font-weight: 300;
    font-size: 18px;
    line-height: 120%;
}

/* 'Mobile' view is very close to 'Other' type of cards */

.news-card.mobile {
    flex-direction: column;
    width: 100%;
    box-sizing: border-box;
    margin: 0;
    padding: 40px;
}

.news-card.mobile .news-card-image {
